import styled from 'styled-components';

const CityCardTitle = () => {
  return (
    <CityCardTitleStyle>
      <span>
        {'>'}  #2, ,  ,  
        5
      </span>
    </CityCardTitleStyle>
  );
};

export default CityCardTitle;

const CityCardTitleStyle = styled.div`
  display: flex;
  align-items: center;

  span {
    line-height: 20px;
    max-height: 20px;
    overflow: hidden;
    text-overflow: ellipsis;

    color: #222;
    word-break: break-all;
    font-size: 16px;
    line-height: 20px;
  }
`;
